@import url('https://fonts.googleapis.com/css?family=Mukta');
.login{
    font-family: 'Mukta', sans-serif;
	height:100vh;
	min-height:550px;
	background-image: url(http://www.planwallpaper.com/static/images/Free-Wallpaper-Nature-Scenes.jpg);
	background-repeat: no-repeat;
	background-size:cover;
	background-position:center;
	position:relative;
    overflow-y: hidden;
}
.login-reg-panel{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
	text-align:center;
    width:70%;
	right:0;left:0;
    margin:auto;
    height:400px;
    background-color: rgba(236, 48, 20, 0.9);
}
.white-panel{
    background-color: rgba(255,255, 255, 1);
    height:500px;
    position:absolute;
    top:-50px;
    width:50%;
    right:calc(50% - 50px);
    transition:.3s ease-in-out;
    z-index:0;
    box-shadow: 0 0 15px 9px #00000096;
}
.login-reg-panel input[type="radio"]{
    position:relative;
    display:none;
}
.login-reg-panel{
    color:#B8B8B8;
}
.login-reg-panel #label-login, 
.login-reg-panel #label-register{
    border:1px solid #9E9E9E;
    padding:5px 5px;
    width:150px;
    display:block;
    text-align:center;
    border-radius:10px;
    cursor:pointer;
    font-weight: 600;
    font-size: 18px;
}
.login-info-box{
    width:30%;
    padding:0 50px;
    top:20%;
    left:0;
    position:absolute;
    text-align:left;
}
.register-info-box{
    width:30%;
    padding:0 50px;
    top:20%;
    right:0;
    position:absolute;
    text-align:left;
    
}
.right-log{right:50px !important;}

.login-show, 
.register-show{
    z-index: 1;
    display:none;
    opacity:0;
    transition:0.3s ease-in-out;
    color:#242424;
    text-align:left;
    padding:50px;
}
.show-log-panel{
    display:block;
    opacity:0.9;
}
.login-show a{
    display:inline-block;
    padding:10px 0;
}
.forgot{
    text-decoration:none;
    color:#2c7715;
}
/* Make sure input text is visible */
.login-reg-panel input.form-control,
.login-reg-panel input[type="text"],
.login-reg-panel input[type="email"],
.login-reg-panel input[type="password"] {
    color: #000 !important;       /* Black text */
    background-color: #fff !important; /* White background */
    border: 1px solid #ccc;       /* Optional border for clarity */
    padding: 10px;
}

/* Placeholder color */
.login-reg-panel input::placeholder {
    color: #555 !important;       /* Dark gray placeholder */
    opacity: 1;
}

/* Button styling (if needed) */
.login-reg-panel .btn-danger {
    background-color: #dc3545;
    border: none;
    color: #fff;
}
